#{$microapp-selector} .support-matrix-view {
    font-size: $next-global-font-size;

    .support-matrix {

        margin-bottom: 20px;

        .dropdown {
            margin-left: 0.25em;
            display: inline-block;

            border: 1px solid $light-grey-01;
            border-radius: $border-radius-base;
            padding: $padding-base-vertical $padding-base-horizontal;

            a {
                cursor: pointer;
                .caret { margin-left: 4px; }
            }
        }

        .support-type-label {
            font-size: inherit;
        }

        .support-type-list {
            max-height: 400px;
            overflow-y: scroll;
        }

        .dropdown-header {
            font-size: 20px;
            font-weight: bold;
            color: inherit;
        }

        .dropdown-menu {
            li {
                padding: 0 10px;

                a {
                    color: $standard-text-color;
                    &:hover {
                        color: #fff;
                    }
                }
            }
        }
    }

    .category-footnotes {
        font-size: 0.875em;
        margin: 1.5em 0;
    }

}
