#{$microapp-selector} {

    $ops-label-color: #606060;
    .tooltip-label {
        color: $ops-label-color;
        text-transform: uppercase;
    }

    .ops-label {
        @extend .tooltip-label;
        font-size: $font-size-small;
        font-weight: normal;
        text-transform: uppercase;
    }

    .ops-dashboard-view {
        .perf-panel {
            .performance-info {
                cursor: help;
            }
        }
        .protection-panel {
            .protection-info {
                cursor: help;
            }
        }

        .capacity-panel {
            .capacity-info {
                cursor: help;
            }
            .popover {
                max-width: 600px;
            }
        }

        [data-region="performance"] {
            max-height: 410px;
        }

        [data-region="system"] {
            max-height: 195px;
        }

        [data-region="protection"] {
            max-height: 220px;
        }

        [data-region="top-vms"][data-vm-vision="true"] {
            max-height: 433px;
        }

        .icon {
            background-position: 0px 1px;
            background-repeat: no-repeat;
            display: inline-block;
        }

        .header {
            text-transform: uppercase;
            padding-top: 5px;
            padding-bottom: 15px;
        }

        .fa-info-circle {
            color: $link-color;
        }

        .large-number {
            font-size: 2.5em;
            > span {
                font-size: 0.4em;
                padding-left: 8px;
            }
        }

        .dashboard-performance-view {
            .highcharts-container {
                overflow: visible !important;
                .highcharts-axis span {
                    width: 100px;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }
            }
        }

        .dashboard-protection-view {

            width: 354px;
            height: 190px;

            .highcharts-container {

                overflow: visible !important;

                .protection-legend-item {
                    cursor: auto !important;
                    width: 130px;

                    > span:first-child {
                        @extend .ops-label;
                        width: 90px;
                        display: inline-block;
                    }

                    > span:last-child {
                        text-align: right;
                        width: 40px;
                        display: inline-block;
                    }

                    &.lag {
                        margin-top: 1em;
                        margin-left: -4px; // It works. I can't tell where the offset is coming from on the second div
                    }
                }
            }
            .highcharts-title {
                text-align: center;
            }

            &.protection-donut {
                .highcharts-title {
                    width: 100px;
                    .large-number {
                        letter-spacing: -0.1em;
                        margin-right: 6px;
                    }
                }
            }
        }

        .dashboard-capacity-view {

            .critical-volume-icon {
                margin-top: 15px;
                width: 79px;
                height: 60px;
                background-image: url(../images/nmbl-dashboard-space-error.png);
            }
            .warning-volume-icon {
                width: 50px;
                height: 32px;
                background-image: url(../images/nmbl-dashboard-space-warning.png);
            }
            .good-volume-icon {
                width: 50px;
                height: 32px;
                background-image: url(../images/nmbl-dashboard-space-ok.png);
            }
            .critical-icon {
                padding-right: 5px;
            }
            .critical-count {
                padding: 12px 35px 5px 10px;
            }
            .bottom-row {
                clear: left;
                padding-top: 20px;
            }
            .range {
                padding-top: 12px;
                padding-left: 5px;
            }
            .count {
                padding-left: 20px;
                padding-top: 10px;
            }
            .good-container {
                padding-left: 30px;
            }
            a[data-count="0"] {
                color: $text-color;
            }

            .dashboard-capacity-bucket {
                display: inline-block;

                width: 30%;
                /*
                    change to 20% if .good is enabled
                */

                background-color: #fff;
                margin-top: 0;
                margin-bottom: 1em;

                margin-left: 1.2%;
                margin-right: 1.2%;

                padding-top: 0.75em;
                padding-bottom: 0.75em;
            }

            .dashboard-capacity-bucket.header,
            .dashboard-capacity-bucket.ops-label,
            .dashboard-capacity-bucket .ops-label {
                white-space: nowrap;
            }

            .dashboard-capacity-bucket .ops-label {
                color: inherit;
            }

            .dashboard-capacity-bucket .fa {
                vertical-align: middle;
                color: #999;

                margin-right: .25em;
            }

            .dashboard-capacity-bucket.row-label {
                font-size: 12px;
            }

            .dashboard-capacity-bucket.critical {
                border: 1px solid #c00;
            }
            .dashboard-capacity-bucket.critical a {
                color: #c00;
            }

            .dashboard-capacity-bucket.warning {
                border: 1px solid #ff8000;
            }
            .dashboard-capacity-bucket.warning a {
                color: #ff8000;
            }

            .dashboard-capacity-bucket.good {
                display: none;
                border: 1px solid #008000;
            }
            .dashboard-capacity-bucket.good a {
                color: #008000;
            }

            .dashboard-capacity-bucket.empty,
            .dashboard-capacity-bucket.empty a {
                border: none;
                background-color: #f6f6f6;
                color: lightgrey;
            }

            .dashboard-capacity-bucket.column-label,
            .dashboard-capacity-bucket.column-label .header {
                margin-bottom: .5em;
                padding-bottom: 0;

                border: none;
            }

            .dashboard-capacity-bucket a:focus {
                outline: none;
            }
            .dashboard-capacity-bucket.empty a:focus,
            .dashboard-capacity-bucket.empty a:hover {
                text-decoration: none;
                pointer-events: none;
                cursor: default;
            }

        }

        .dashboard-system-view {

            .icon {
                vertical-align: middle;
                width: 45px;
                height: 38px;
            }

            .hardware-icon {
                margin-top: 9px;
                width: 60px;
                height: 60px;
                background-image: url(../images/nmbl-dashboard-hw.png);
            }

            a:focus {
                outline: none;
                text-decoration: none;
            }

            [data-has-warnings="false"] {
                a:hover {
                    cursor: default;
                    text-decoration: none;
                }
            }

            [data-has-warnings="true"] {
                a:hover {
                    cursor: pointer;
                }
            }

            .hb-container {
                padding-left: 6px;
                &[data-has-warnings="true"] .icon {
                    background-image: url(../images/nmbl-dashboard-heartbeat-down.png);
                }
                &[data-has-warnings="false"] .icon {
                    background-image: url(../images/nmbl-dashboard-heartbeat-up.png);
                }
            }
            .asup-container {
                &[data-has-warnings="true"] .icon {
                    background-image: url(../images/nmbl-dashboard-asup-down.png);
                }
                &[data-has-warnings="false"] .icon {
                    background-image: url(../images/nmbl-dashboard-asup-up.png);
                }
            }

            .good-container {
                width: 57px;
                padding-left: 7px;
            }
            .hardware-warning-container {
                width: 55px;
                padding-left: 7px;
                padding-top: 20px;
                a:hover {
                    text-decoration: none;
                    color: darken($brand-danger, 15%);
                }
            }
            .warning-count {
                font-size: 1.5em;
            }
            .software {
                display: none; // temporarily disable on Lasik until we have a replacement for assets page
                clear: both;
                padding-top: 30px;
            }
            .asup-container {
                padding-left: 15px;
            }
            .connectivity-container {
                padding-left: 6px;
            }
        }

        .asup-warnings,
        .heartbeat-warnings {
            white-space: nowrap;
            td:last-child {
                padding-left: 10px;
            }
        }

        [data-region="performance"] div.highcharts-tooltip > span {
            background-color: white;
            border-radius: 3px;
            z-index: $zindex-tooltip;
            width: 260px;
            padding: 0px;

            ul.peer-performance-tooltip {
                list-style: none;
                margin: 1em;
                padding: 1px;

                li {
                    color: $gray-dark;
                    text-transform: uppercase;

                    hr {
                        margin: 8px 0;
                        border-color: $gray;
                    }

                    span.r {
                        color: $text-color;
                        text-transform: none;
                        float: right;
                    }
                }
            }
        }

        .asset-capacity-warnings td {
            white-space: nowrap;
        }

    }

    .hardware-warnings {
        $icon-height: 30px;
        $ops-label-color: #606060;

        table {
            table-layout: fixed;
            width: auto;
            margin-bottom: 0px;
        }

        td {
            vertical-align: middle;
            white-space: nowrap;
            // overflow: hidden;
            text-overflow: ellipsis;

            border-top: 1px solid $table-border-color;
            border-bottom: 1px solid $table-border-color;
            padding-left: 12px;
            padding-right: 12px;

            &[data-column="array"] {
                max-width: 170px;

                .timestamp {
                    font-size: $font-size-small;
                    color: $ops-label-color;
                    margin-top: 10px;
                }
            }

            &[data-column="controllers"] {
                width: 220px;
            }

            &[data-column="ssd"],
            &[data-column="hdd"] {
                font-size: $font-size-large;
                text-align: center;
                color: $ops-label-color;
                small {
                    font-size: $font-size-small;
                }
            }
        }

        .controller-list {
            // @extend .list-unstyled;
            list-style: none;
            line-height: $icon-height;
            vertical-align: middle;
            margin-bottom: 0px;

            > li {
                margin-bottom: 10px;

                &:last-child {
                    margin-bottom: 0px;
                }
            }
        }

        .component-list {
            @extend .list-inline;
            line-height: $icon-height;
            display: inline-block;
            [data-component="temperature"] {
                display: none;
            }
            margin-left: 15px;
        }

        .component-icon {
            width: 30px;
            height: $icon-height;
            vertical-align: middle;
            display: inline-block;
        }

        [data-controller-state] {
            text-transform: uppercase;
            display: inline-block;
            width: 63px;
            margin-left: 15px;
        }

        [data-controller-state="solo"],
        [data-controller-state="active"] {
            background-color: $label-info-bg;
        }

        [data-controller-state="standby"] {
            background-color: $label-default-bg;
        }

        [data-controller-state="down"] {
            background-color: $label-danger-bg;
        }

        [data-controller-state="stale"],
        [data-controller-state="none"] {
            background-color: $warning-color;
        }

        [data-component="fan"] {
            background-image: url(../images/nmbl-array-fan-error.png);
            &[data-status="OK"] {
                background-image: url(../images/nmbl-array-fan-gray.png);
            }
        }

        [data-component="temperature"] {
            background-image: url(../images/nmbl-array-temp-error.png);
            &[data-status="OK"] {
                background-image: url(../images/nmbl-array-temp-gray.png);
            }
        }

        [data-component="psu"] {
            width: 16px;
            height: 16px;
            background-image: url(../images/nmbl-array-pwr-error.png);
            &[data-status="OK"] {
                background-image: url(../images/nmbl-array-pwr-gray.png);
            }
        }

        .tooltip-inner {
            max-width: none;
            white-space: nowrap;
        }
    }

    .vmops-view .dashboard-topvms-view {
        .total-value {
            display: none;
        }
    }

    .dashboard-topvms-view {

        .number-text {
            padding-left: 4px;
        }

        .highcharts-container .highcharts-xaxis-labels span {
            width: 100px;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis !important;
            text-align: right;
            font-size: 12px !important;
        }

        .highcharts-tooltip > span {
            background-color: #fff;
            z-index: $zindex-tooltip;
        }
    }
}
