@import "array-upgrade-card";
@import "software-upgrades-dialog";

#{$microapp-selector} {
    /*!
     * Base Card Styles
     */

    @media print {
        .array-card {
            box-shadow: none !important;
            page-break-before: auto;
            page-break-inside: avoid;

            .array-card-container {
                background: white;
                float: none !important;
                display: inline-block !important;
                margin-right: 0px !important;
            }

            .array-card-main-content {
                margin-right: 15px !important;
            }

            .array-card-side-bar {
                float: none !important;
                display: inline-block !important;
                vertical-align: top !important;
            }
        }

        .details-link {
            display: none;
        }
    }

    .array-card {
        border: 1px solid #d2d2d2;
        border-radius: 5px;
        box-shadow: 2px 2px 2px #d2d2d2;
        padding: 10px;
        margin-bottom: 20px;
        font-family: "RobotoCnRegular", Arial, Helvetica, sans-serif;
        position: relative;

        .array-summary-header {
            margin-bottom: 10px;
        }

        .array-card-container {
            width: 100%;
            float: left;
            margin-right: -200px;

            .array-card-main-content {
                margin-left: 15px;
                margin-right: 190px;

                .asset-capacity {
                    float: left;
                    width: 50%;
                }
                .asset-performance {
                    float: left;
                    width: 50%;
                    position: relative;
                }
            }
        }

        .array-card-side-bar {
            float: left;
            right: 0;
            width: 180px;
            border-radius: 5px;
            padding: 5px 5px 5px 10px;
            font-size: 13px;

            TABLE TD {
                //vertical-align: top;
            }

            tr[data-action="wellness"] {
                cursor: pointer;
            }
        }

        .details-link {
            font-size: 1.2em;
            position: absolute;
            right: 20px;
            bottom: 15px;
            text-decoration: underline;
        }
    }

    .array-card-footer {
        clear: both;
    }

    .array-card .asset-footer {
        padding-top: 15px;
    }

    .array-summary-header {

        .model-info-icon {
            display: inline-block;
            cursor: help;
        }

        .shelf-circle {
            display: inline-block;
            border-radius: 50%;
            width: 15px;
            height: 15px;
            padding: 2px;
            color: white;
            text-align: center;
            margin: 3px;
            background-color: #274b6d;
        }

        .array-hostname {
            font-weight: bold;
        }

        .separator {
            margin-left: 4px;
            color: #ccc;
            margin-right: 4px;
        }
    }

    .array-card-footer .asset-footer .footer-button {
        display: none;
    }
    .asset-footer {
        $icon-color: #28658d; // Match color of image icons
        $button-vertical-padding: 2px;

        .list-inline > li {
            vertical-align: bottom; // Align text to image icons

            &:not(.footer-button) {
                padding-bottom: $button-vertical-padding;
            }
        }

        .btn {
            @include button-variant($icon-color, #fff, $icon-color);
            padding: $button-vertical-padding 6px;
        }

        .event-circle {
            display: inline-block;
            border-radius: 25%;
            width: 25px;
            padding: 3px;
            color: white;
            text-align: center;
            margin: 5px;
        }

        .array-footer-icon {
            background-repeat: no-repeat;
            height: 26px;
            width: 26px;
        }

        .array-location-icon {
            @extend .array-footer-icon;
            color: #28658d;
            width: 24px;
            height: inherit;
        }

        .call-home-icon {
            @extend .array-footer-icon;
            background-image: url('../images/is-callhome.png');
        }

        .call-home-warning-icon {
            @extend .array-footer-icon;
            background-image: url('../images/is-callhomeWarning.png');
        }

        .call-home-error-icon {
            @extend .array-footer-icon;
            background-image: url('../images/is-callhomeError.png');
        }

        .heartbeat-icon {
            @extend .array-footer-icon;
            background-image: url('../images/is-heartbeats.png');
        }

        .heartbeat-warning-icon {
            @extend .array-footer-icon;
            background-image: url('../images/is-heartbeatsWarning.png');
        }

        .heartbeat-error-icon {
            @extend .array-footer-icon;
            background-image: url('../images/is-heartbeatsError.png');
        }

        .support-icon {
            @extend .array-footer-icon;
            background-image: url('../images/is-certificate.png');
        }

        .support-error-icon {
            @extend .array-footer-icon;
            background-image: url('../images/is-certificateError.png');
        }

        .event_summary {
            text-decoration: none;
        }

        .separator {
            margin-left: 4px;
            color: #ccc;
            margin-right: 4px;
        }
    }

    [data-name="asset-grid-view"] {
        .support-error-icon {
            @extend .array-footer-icon;
            background-image: url('../images/is-certificateError.png');
        }
    }

    .array-card .no-array-info {
        margin-top: 5px;
    }

    .ui-tooltip.details-link-tooltip {
        width: 200px;
        max-width: 200px;
    }

    .ui-tooltip.asset-details {
        max-width: 500px;
        font-size: .8em;

        .delayed {
            font-weight: bold;
            color: $urgent-color;
        }

        .warning {
            font-weight: bold;
            color: $critical-color;
        }

        .key {
            font-weight: bold;
        }

        .tooltip-title {
            font-size: 1.1em;
            font-weight: bold;
            color: $nimble-green;
        }

        .tooltip-warning {
            font-size: 1.1em;
            font-weight: bold;
            color: $important-color;
        }

        .tooltip-error {
            font-size: 1.1em;
            font-weight: bold;
            color: $critical-color;
        }

        .tooltip-grid {
            border-collapse: collapse;
            width: 400px;

            td, th {
                border-right: 1px solid #d8d8d8;
                padding: 0px 8px;
                text-align: left;
            }
            td {
                border-top: 1px solid #d8d8d8;
            }
            td:last-child, th:last-child {

                border-right: none;
            }
        }

        .spinner {
            margin: 5px 0 5px 5px;
        }
    }
}
