#{$microapp-selector} {
    $font-size-small: 14;
    $inverted-text-color: #fff;
    $gray: #ccc;

    div[data-overlay] {
        position: relative;
        width: 0px;
    }

    .performance-chart-view {
        position: relative;

        .range-selector {
            @include clearfix();

            .button-group {
                float: right;
            }

            margin-bottom: 10px;
        }

        .highcharts-title {
            text-transform: uppercase;
            font-weight: bold;

            // HACK Highcharts keeps incorrectly applying yCorrection to the title (see getSpanCorrection)
            // I'm not able to determine which Highcharts options to specify to avoid this
            top: 0 !important;
        }

        .highcharts-tooltip {

            ul {
                // @include list-unstyled;

                display: inline-block;

                > li {
                    display: inline-block;
                    margin-right: 15px;
                }
            }

            .name {
                font-weight: bold;
                text-transform: uppercase;
            }

            .timestamp {
                display: inline-block;
                text-align: right;
                width: 140px;
            }
        }

        [data-region="iops"] .highcharts-tooltip {
            .impact-score {
                font-size: 10px;
                text-transform: uppercase;
            }
        }

        .chart-group {
            border-top: 1px solid $light-grey-01;
            > h5 {
                text-transform: uppercase;
                font-weight: bold;
                margin-bottom: -5px;
            }

            &:first-child {
                border-top: 0px;
            }
        }

        .chart-summary {
            font-size: $font-size-small;

            h6 {
                text-transform: uppercase;
                font-weight: bold;
                margin-top: 5px;
                margin-bottom: 2px;
            }

            .unit {
                font-weight: normal;
                text-transform: none;

                &:before {
                    content: ' ('
                }
                &:after {
                    content: ')'
                }
            }
            .panel-heading,
            .panel-body {
                padding: 5px;
                color: inherit;
            }

            .bar {
                line-height: 1;
                height: $font-size-small;
                margin-bottom: 2em;

                width: calc(100% - 7ch);
                position: relative;

                .popover-content {
                    white-space: nowrap;
                }
            }

            .bar-fill {
                float: left;
                height: 1em;
            }

            .bar-label {
                position: absolute;
                padding-left: 0.5em;
            }
        }

        [data-region="factorsChart"] .chart-summary {
            label {
                float: right;
                color: inherit;
                font-weight: bold;
            }

            p {
                line-height: 12px;
            }

            [data-highcharts-chart] {
                border-top: 1px solid $gray;
                padding-top: 2px;
                margin-top: 2px;
            }

            input[name="threshold"] {
                width: 50px;
                font-weight: normal;
                text-align: right;
            }

            .threshold-unit {
                font-weight: bold;
                margin-left: 4px;
            }
        }

        [data-region="events"] {
            margin-top: -15px;
        }

        [data-region="spinner"] {
            width: 100%;
            height: 100%;
            position: absolute;
            bottom: 2.5rem;
        }
    }

    [data-crosshair] {
        position: absolute;
        top: 0;
        z-index: 20;
        height: 100vh;

        [data-crosshair-segment] {
            position: absolute;
            background: transparent;
            pointer-events: none;
            border-left: 1px solid $gray;
            border-left-style: solid;
        }

        .events {
            display: none;
            position: absolute;
            background-color: white;
            max-width: 325px;
            font-size: 0.85em;
            color: $inverted-text-color;
            white-space: nowrap;
            pointer-events: none;
        }

        &.highlight {
            [data-crosshair-segment] {
                border-left-width: 2px;
            }

            .events {
                display: block;
                margin-bottom: -4px;

                &.left {
                    margin-left: -4px;
                }

                &.right {
                    margin-right: -6px;
                    right: 0;
                }

                ul {
                    margin: 4px;
                    padding: 5px;
                }

                li {
                    margin-bottom: 2px;
                    &:last-child {
                        margin: 0;
                    }

                    &.date {
                        opacity: 0.8;
                    }
                }
            }
        }
    }
}
