#{$microapp-selector} {
    .planning-view {

        font-size: 1em;

        label {
            font-weight: normal;
        }

        H3 {
            color: #274b6d;
            font-size: 0.9em;
            margin-bottom: 5px;
        }

        .planning-output-area {

            .output-container {
                float: left;

                .header {
                    margin-bottom: 5px;
                }
            }

            .change-rate-container {
                @extend .output-container;

                $bandwidth-container-height: 75px;

                .total-bandwidth-container {
                    border: 1px solid #274b6d;
                    height: $bandwidth-container-height;
                    margin-right: 20px;
                    width: 230px;

                    .no-volumes {
                        line-height: 36px !important;
                    }

                    .total-bandwidth {
                        color: #274b6d;
                        font-size: 1.8em;
                        line-height: $bandwidth-container-height;
                        margin: auto;
                        text-align: center;
                        vertical-align: middle;
                    }
                }
            }

            .replication-role-container {
                @extend .output-container;

                .cbox-container {
                    width: 200px;
                }

                #filter-list-roles {
                    width: 150px;
                }
            }

            #planning-chart {
                float: left;
                width: 100%;
            }
        }

        .batch-links-toolbar {
            margin-top: 30px;
            padding: 8px 0;

            .separator {
                color: #ccc;
            }
        }

        .planning-filter-input-region {
            margin-top: 10px;

            .planning-filter-column {
                float: left;
                width: 20%;
                margin-bottom: 30px;

                .nmbl-filter-list .cbox-container {
                    max-height: 400px;
                    font-size: 1em;
                }
            }
        }

    }

    @media print {
        .planning-view {
            .batch-links-toolbar {
                display: none;
            }
            .planning-filter-column {
                float: none !important;
                width: 100% !important;
            }
            .nmbl-filter-list {
                .search-field {
                    display: none;
                }
                .toolbar {
                    display: none;
                }
                .cbox-container {
                    font-size: 11px;
                    max-width: none !important;
                    max-height: none !important;
                }
                .summary-footer {
                    font-size: 11px;
                }
            }
        }
    }
}
