#{$microapp-selector} {

    /*!
     * Dashboard Styles
     */

    .dashboard-view {

        H2 {
            color: #274b6d;
            margin-left: 7px;
        }

    }

    .dashboard-view .dashboard-content .dashboard-section {
        margin-bottom: 30px;
    }

    .space-savings-container {
        @media print {
            margin-bottom: 150px !important;
        }
    }

    /*
     * Data Protection Widget
     */
    @media print {
        .dashboard-data-protection-tab {
            page-break-before: always;
            page-break-inside: avoid;
        }
    }

    .data-protection-view {

        .section {
            // width: 50%;
            // height: 350px;
            // float: left;
            margin-bottom: 20px;
        }

        .rpo-container {
            @extend .section;
        }

        .local-retention-container {
            @extend .section;
        }

        .disaster-recovery-container {
            @extend .section;
            // width: 700px;
        }

    }

    /*
     * Case Statistics View
     */
    .case-statistics-view {
        .case-summary {
            margin-bottom: 20px;
        }
    }

    .outer-center {
        float: right;
        right: 50%;
        position: relative;
    }
    .inner-center {
        float: right;
        right: -50%;
        position: relative;
    }

    .dashboard-success-message {
        background-image: url('../images/checkmark-small.png');
        background-repeat: no-repeat;
        padding-left: 25px;
        white-space: normal;
    }

    /**
     * The Upgrade Summary Item View widget
     */
    .upgrade-summary-item-view {
        border-right: 1px solid #ccc;
        margin-right: 10px;

        TABLE {
            table-layout: fixed;
            font-size: 0.85em;

            TH {
                color: gray;
                padding-bottom: 5px;
            }

            TR {
                margin-bottom: 3px;
            }

            TD {
                vertical-align: top;
                padding-bottom: 5px;
            }

            .name-column {
                width: 65px;
                color: gray;
            }

            .first-value-column {
                width: 130px;
            }

            .second-value-column {
                width: 130px;
            }

            .recommended-model {
                color: #4d759e;
            }
        }
    }

    .upgrade-section {
        page-break-before: always;

        .upgrade-section-notes {
            margin: 0 0 10px 10px;
        }
    }

    .upgrade-needs-container {

        .summary-column {
            float: left;
            width: 460px;
            margin-right: 100%;
        }

        .performance-column {
            /*float: left;*/
            width: 100%;
        }
    }

    .upgrade-item-view {
        border: 1px solid #ccc;
        border-radius: 5px;
        margin-bottom: 10px;
        padding: 10px;

        .header {
            font-weight: bold;
            margin-bottom: 8px;
        }

        .left-column {
            float: left;
            width: 690px;
            margin-left: -100%;
        }

        H3 {
            clear: left;
            /* float: left; */
        }

        .upgrade-content {
            float: left;
            /* width: 450px; */
            /* min-width: 500px; */
        }

        .capacity-content {
            float: left;
            width: 270px;
            /*width: 33.33%;*/
            margin-top: -15px;
        }

        .content-wrapper {
            float: left;
            width: 100%;

            .performance-upgrade-content {
                /* float: left; */
                /* width: 100%; */
                width: auto;
                margin-left: 620px;
                height: 100px;
                /* width: 250px; */
                /* width: 33.33%; */
            }
        }
    }

    .upgrade-performance-item-view {
        // margin-top: -15px;
        height: 140px;
        border-left: 1px solid #ccc;
    }

    .dataProtection-drilldown {
        .toolbar {
            .ui-widget {
                display: inline-block;
                margin-right: 20px;
                white-space: nowrap;
            }
        }
    }

    .ui-autocomplete-input {
        width: 150px;
    }

    #dataProtection-volumes {
        @extend .ui-autocomplete-input;
        width: 200px;
    }

    .ui-autocomplete {
        width: 300px;
        max-height: 250px;
        overflow-y: auto;
        /* prevent horizontal scrollbar */
        overflow-x: hidden;
    }

    .dashboard-space-view {

        .space-usage-header {
            margin-top: 60px;
        }
    }

    .space-usage-view {

        .space-usage-summary {
            float: left;
        }
        .space-usage-table-container {
            float: left;
            margin-left: 10px;
            width: 530px;

            TABLE {
                border: 1px solid #ccc;
                font-size: .8em;
            }
        }
    }

    .dashboard-tabs .ui-tabs .ui-tabs-panel {
        background-color: white !important;
    }

    .dashboard-tabs .ui-widget-content {
        background-color: white !important;
    }

    .no-dashboard-content {
        border: 1px solid #ccc;
        padding: 5px 10px 5px 10px;

        .ui-icon {
            display: inline-block !important;
            vertical-align: top;
        }
    }

    .dashboard-view .dashboard-content #executive-dashboard-toolbar {
        text-align: right;
    }

    #executive-dashboard-settings-form {
        .control-label {
            text-align: left;
            margin-left: 50px
        }
        #executive-dashboard-email-form {
            margin-left: 15px
        }
        .executive-dashboard-email-list {
            margin-left: 150px
        }
    }
}
