.software-view {
    font-size: $next-global-font-size;
    margin-top: 8px;

    table {
        border-collapse: collapse;

        th, td, tr {
            border: 1px solid $light-grey-01 !important;
        }
    }

    [data-region="sidePanel"] {
        ul {
            margin: 0;
        }

        .software-category li {
            padding-bottom: 5px;
            word-wrap: break-word;
        }
    }

    [data-region="whitelistCategoryPanel"] {
        ul {
            margin: 0;
        }
    }

    [data-region="mainPanel"] {
        h2, h3 {
            margin-bottom: 8px;
            margin-top: 20px;
            color: nth($brand-accent-colors, 2);
        }
    }

    .product-view {
        > h2:first-child {
            margin-top: 0;
        }

        table {
            table-layout: fixed;
        }

        ul {
            margin: 0 1.25em;
        }

        .other-versions-link-container {
            font-size: $font-size-base;
            font-weight: normal;
            color: $text-color;
            margin-left: 5px;
        }

        [data-column-header="version"] {
            width: 15ch;
        }
        [data-column-header="release-date"] {
            width: 15ch;
        }
        [data-column-header="downloads"] {
            width: 32ch;
        }
        [data-column="downloads"] {

            // Avoid wrapping for longer names...
            li {
                padding-bottom: 5px;
                text-overflow: ellipsis;
                overflow : hidden;
                white-space: nowrap;

                // ...but show the full wrapped name on hover
                &:hover {
                    text-overflow: clip;
                    white-space: normal;
                    word-break: break-all;
                }
            }
        }
    }

    [data-region="whatsNew"] {
        ul {
            list-style-type: none;
        }
        li {
            padding-bottom: 5px;
        }
    }

    [data-region="nimbleOsReleases"] {
        table {
            border: 1px solid $table-border-color;
            table-layout: fixed;
            width: auto;
        }

        [data-column-header="version"] {
            width: 12ch;
        }
        [data-column-header="release-date"] {
            width: 15ch;
        }
        [data-column-header="status"] {
            width: 25ch;
        }
        [data-column-header="release-notes"] {
            width: 15ch;
        }
    }
}

.eula-body-container {
    height: 500px;

    iframe {
        width: 100%;
        height: 100%;
    }
}
