#{$microapp-selector} {
    // the array upgrade card
    .array-card {
        .upgrade-card-column {
            width: 33%;
        }

        &[data-array-type="all-flash"] {
            .upgrade-card-column {
                width: 50%;
                &.cache-chart {
                    display: none;
                }
            }
        }

        .upgrade-contents {

            padding-top: 10px;
            font-size: .85em;

            .upgrade-columns-container {
                width: auto;
                overflow: hidden;
            }

            .upgrade-card-recommendation-column {
                float: right;
                width: 275px;
                margin-left: 10px;
                margin-top: -2px;

                .component-table {
                    width: 100%;
                    margin-bottom: 5px;
                    table-layout: fixed;

                    TH {
                        font-weight: normal;
                        border-bottom: 2px solid #6678b1;
                        padding: 0 8px 8px 8px;
                    }

                    TD.cache-table-container-cell {
                        padding-right: 0;
                    }

                    TD {
                        border-bottom: 1px solid #ccc;
                        font-size: 0.9em;
                        padding: 3px 12px 3px 8px;

                        // the nested cache table
                        TABLE.cache-table {
                            width: 100%;
                            margin: 3px 0 3px 0px;

                            TR:nth-last-child(2) {
                                TD {
                                    border-bottom: 2px solid #ccc;
                                }
                            }

                            TR:last-child {
                                TD {
                                    border-bottom: none;
                                }
                            }

                            TR:last-child {
                                padding-right: 0;
                            }

                            TD:first-child {
                                width: 80px;
                            }

                            TD {
                                font-size: 11px;
                                padding: 3px 8px 3px 8px;
                            }
                        }
                    }

                    .current-value {
                        width: 73px;
                        text-align: right;
                    }

                    .recommended-value {
                        color: #4d759e;
                        text-align: right;
                        width: 72px;
                    }
                }
            }

            .upgrade-card-column {
                float: left;
                max-height: 250px;

                .column-header {
                    font-weight: bold;
                    text-align: center;
                    color: #4d759e;

                }

                .upgrade-list {
                    margin-top: 10px;
                    margin-left: 15px;

                    .upgrade-recommendation {
                        text-align: center;
                        margin-bottom: 5px;
                        font-size: 0.85em;

                        TABLE {
                            margin-left: 25px;
                            text-align: left;
                        }
                    }
                }

                .temporary-loading-text {
                    margin: 30px auto 30px auto;
                    width: 70px;
                    padding-left: 20px;
                    color: #4d759e;
                }
            }

            .ui-icon {
                display: inline-block !important;
                vertical-align: middle;
            }

            .upgrade-note {
                padding: 5px;
                margin-top: 5px;
            }

        }

        .upgrade-card-column.last-column {
            border-right: 1px solid #ccc;
            padding-right: 10px;
        }
    }
}
