@media print {
    // these 3 selectors need to be global
    // otherwise styling for Executive Dashboard PDF wont work
    #main {
        min-width: auto !important;
        height: auto;
    }

    .elmer-GlobalBanner,
    .elmer-NavBar {
        display: none !important;
    }

    #{$microapp-selector} {
        :global .column .highcharts-container {
            transform-origin: top left;
            transform: scale(0.612);
        }

        :global .data-protection-view .highcharts-container {
            transform-origin: top left;
            transform: scale(0.612);
        }

        :global .array-summary-header {
            width: 80%;
        }
        :global .array-card-contents {
            width: 90%;
        }

        :global .upgrade-card-column .highcharts-container {
            transform-origin: top left;
            transform: scale(0.5);
        }

        :global .elmer-ScrollContainer {
            display: block !important;
        }

        .hidden-print,
        .elmer-SecondaryNavBar {
            display: none !important;
        }
    }
}
