#{$microapp-selector} .software-upgrades-dialog {

    .header {
        display: table;
        width: 100%;
        padding-bottom: 1em;
    }

    dl {

        display: table-cell;
        vertical-align: middle;

        dt {
            display: inline;
            margin: 0 0 0 0;
            padding-left: 2em;
            color: gray;

            &:first-child {
                padding-left: 0;
            }

            &:after {
                content: ":";
            }
        }

        dd {
            display: inline;
            margin: 0 0 0 0;
            font-weight: bold;

            span.array-serial {
                font-weight: lighter;
                font-size: 0.85em;
            }
        }

    }

    .software-upgrades-legend {
        display: table-cell;
        vertical-align: middle;
        text-align: center;
        font-size: 10px;
        border: 1px solid black;
        padding: 0 0 3px 0;

        legend {
            width: auto;
            margin-bottom: 0px;
            font-weight: bold;
            font-size: inherit;
        }
    }

    th:focus {
        outline: none;
    }

    td {
        vertical-align: top;
    }

    ul.upgrade-paths {

        margin: 0 0 0 0;
        padding: 0 0 0 0;

        li {
            padding: 0 0 0 0;
            background-image: none;
            list-style-type: none;
        }

        ol.upgrade-path {
            min-width: 250px;
            padding: 0 0 0.5em 0;
            margin-top: 0px;
            margin-bottom: 0px;

            li {
                display: inline;
                background-image: none;
                margin-left: 0px;

                &:after {
                    font-family: 'FontAwesome';
                    content: '\f178'; // fa-long-arrow-right
                    padding-left: .2em;
                }

                &:last-child:after {
                    content: none;
                    padding-left: 0;
                }
            }
        }
    }

    .tooltip-inner {
        text-align: left;
    }
}

.blacklisted-release {
    color: $critical-color;
}

.blacklisted-icon {
    padding-right: 2px;
}

.blacklisted-reason {

    text-align: left;
    border-top: thin solid #d3d3d3;

    th {
        padding-top: 10px
    }

    td {
        text-align: justify;
        padding-left: 20px;
    }
}
