#{$microapp-selector} {
    /*!
     * Highlight related styles
     */

    .highlight-view {
        /* position: absolute; */
        width: 225px;

        TD {
            padding-bottom: 0.5em;
        }

        .highlight-icon-cell {
            margin-top: 2px;
            padding-right: 5px;
            text-align: right;
            width: 16px;
            vertical-align: middle;
            // cursor: help;
        }

        .highlight-message {
            font-family: Verdana, Arial, sans-serif;
            font-size: 0.9em;
        }

        .highlight-time {
            font-family: Verdana, Arial, sans-serif;
            font-size: 0.8em;
            color: #158bda;
            text-decoration: underline;
            cursor: pointer;
        }

        .highlight-item {
            display: inline-block;
            height: 20px;
            width: auto;
            border-radius: 2px;
            text-align: center;
            vertical-align: middle;

            .highlight-item-text {
                margin-left: 1px;
                display: inline-block;
                letter-spacing: .1em;
            }
        }

        .highlight-error {
            @extend .highlight-item;
            display: inline-block;
            height: 20px;
            width: 20px;
            border-radius: 12px;
            min-width: 20px;
            background-color: #ed2826;

            .highlight-item-text {
                @extend .highlight-item-text;
                color: white;
                font-weight: bold;
                font-family: courier new;
                font-size: 1.2em;
                margin-top: 1px;
            }
        }

        .highlight-info {
            @extend .highlight-item;
            display: inline-block;
            height: 20px;
            width: 20px;
            border-radius: 12px;
            background-color: #06b;

            .highlight-item-text {
                @extend .highlight-item-text;
                color: white;
                font-weight: bold;
                font-family: courier new;
                font-size: 1.2em;
                margin-top: 1px;
            }
        }

        .highlight-success {
            @extend .highlight-item;
            display: inline-block;
            height: 20px;
            width: 20px;
            border-radius: 12px;
            background-color: #00a13c;

            .highlight-item-text {
                @extend .highlight-item-text;
                color: white;
                font-weight: bold;
                font-family: courier new;
                font-size: 1.2em;
                margin-top: 1px;
            }
        }

        .highlight-badge {
            @extend .highlight-item;
            font-size: .9em;
            min-width: 40px;
            line-height: 20px;

            .highlight-item-text {
                @extend .highlight-item-text;
                color: white;
            }
        }

    }

    .highlight-tooltip {
        max-width: 300px;
        font-size: .85em !important;
    }
}
