#{$microapp-selector} {

    .button-group input {
        display: none;
    }

    .button-group label.active {
        background: #000;
        color: #fff;
    }

    .well {
        border-radius: 3px;
        border: 1px solid #ccc;

        background: #f9f9f9;

        margin-bottom: 1em;
        padding: .8em;

        h4 {
            font-size: 18px;
            margin-bottom: .5em;
        }

        ul {
            padding-left: 40px;
            margin-bottom: .5em;
        }
    }

    /*
    IE 11 fix for multiple-select component
    */
    @media screen and (-ms-high-contrast: active), screen and (-ms-high-contrast: none) {
        /* IE10+ specific styles go here */
        .grid-filter-wrapper .ms-choice > div, .grid-filter-wrapper .ms-choice > div.open {
            background-position-x: 1.5em;
            background-position-y: 0.75em;
        }
    }

}
