#{$microapp-selector} {
    .documentation-view {
        font-size: $next-global-font-size;
        margin-top: 8px;

        ul {
            margin-left: 0;
        }

        .row:first-child {
            margin-bottom: 20px;
        }

        input.search-box {
            min-width: 50ch;
        }

        ul.search-inner-hits {
            list-style: none;
        }

        .label {
            border-radius: 1em;
        }

        [data-region="breadcrumbs"] {
            background-color: $panel-default-heading-bg;

            ul {
                // @include list-unstyled;
                @extend .list-inline;
                display: inline-block;

                li {
                    // Workaround for https://css-tricks.com/fighting-the-space-between-inline-block-elements/
                    display: inline;
                    margin: 0.25em;
                }
            }

            .label {
                font-size: 90%;
                background-color: $label-default-bg;
                color: $white;

                i {
                    padding-left: 5px;
                }

                .field {
                    font-weight: normal;
                }
            }
        }

        [data-region="pagination"] {
            background-color: $panel-default-heading-bg;
        }

        ul.facets,
        ul.facet-buckets {
            // @include list-unstyled;
        }

        p.highlights {
            color: #666;
            font-size: $font-size-small;
            margin-bottom: 20px;
            max-width: 700px;
        }

        i[data-action^="deselect"] {
            cursor: pointer;
        }

        .facets {
            .facet {
                border-top: 1px solid $light-grey-01;
                padding-bottom: 5px;
                margin-top: 10px;

                &:first-child {
                    border-top: 0;
                    margin-top: 0;

                    h4.facet-title {
                        margin: 0;
                    }
                }

                &.has-selected {
                    .facet-bucket:not(.selected) {
                        display: none;
                    }
                }
            }

            ul.facet-buckets {
                margin-left: 0;
                list-style: none;

                .facet-bucket > .selected {
                    font-weight: bold;
                }
            }

            h4.facet-title {
                margin: 20px 0 0;
            }

            li.facet-bucket {
                .facet-name {
                    overflow-x: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    margin-right: 8px;
                }
            }
        }

        .spinner {
            margin-top: 100px;
        }

        .home-view {
            h1, h2, h3 {
                color: nth($brand-accent-colors, 2);
            }

            table,
            table > tbody > tr > td {
                border-left-width: 0;
                border-right-width: 0;
            }

            [data-column="doc-icon"] {
                vertical-align: top;
                text-align: center;

                > img {
                    width: 100px;
                }
            }
        }

        .language-preferences {
            .popover {
                min-width: 150px;

                button.close {
                    line-height: $font-size-base; // HACK
                }
            }
        }

        .paging {
            a,
            a:hover,
            a:focus {
                &.disabled {
                    color: $gray-dark;
                    cursor: $cursor-disabled;
                    text-decoration: none;
                }
            }
        }

    }

    .documentation-view,
    .help-docs,
    .software-view {
        ul.search-hits {
            .alt-media {
                padding-left: 8px;
            }

            .labels {

                display: inline-block;
                padding-left: 12px;
                @extend .list-inline;

                li {
                    display: inline;
                }
                .label[data-field="version"] {
                    background-color: $light-grey-05;
                    color: lighten($text-color, 30%);
                }
                .label[data-field="visibility"] {
                    background-color: $important-color;
                    color: $white;
                }
            }
        }
    }
}
