#{$microapp-selector} {
    ul.comma-delimited-list {
        list-style: none;
        display: inline;
        line-height: initial; // foundation was mis-aligning font-awesome icons without this
        padding: 0;
        margin-left: 0;

        li {
            display: inline;
            padding: 0;

            &:after {
                content: ", ";
            }

            &:nth-last-child(2):after {
                content: " and ";
            }

            &:last-child:after {
                content: "";
            }
        }
    }
}
