#{$microapp-selector} {

    .critical {
        background-color: $critical-color;
    }

    .urgent {
        background-color: $urgent-color;
    }

    .important {
        background-color: $important-color;
    }

    .forreview {
        background-color: $for-review-color;
    }

    .ok {
        background-color: $ok-color;
    }

    .warning {
        background-color: $for-review-color;
    }

    .error-text {
        color: $critical-color;
        font-weight: normal;
    }

    .critical-text {
        color: $critical-color;
        font-weight: normal;
    }

    .urgent-text {
        color: $urgent-color;
        font-weight: normal;
    }

    .important-text {
        color: $important-color;
        font-weight: normal;
    }

    .warning-text {
        color: $for-review-color;
        font-weight: normal;
    }

    .forreview-text {
        color: $for-review-color;
        font-weight: normal;
    }

    .ok-text {
        color: $ok-color;
        font-weight: normal;
    }
}
