#{$microapp-selector} .overlay-spinner {
    %extend {
        width: 100%;
        height: 100%;
        z-index: $zindex-modal-background;
        position: absolute;
        top: 0;
        left: 0;
    }

    @extend %extend;
    visibility: hidden;
    pointer-events: none;

    &.on {
        visibility: visible;

        .overlay {
            @extend %extend;
            background-color: $light-grey-05;
            opacity: $modal-backdrop-opacity;
            pointer-events: auto;
        }

        .spinner {
            @extend %extend;
            display: flex;
            overflow: hidden;
            justify-content: center;
            //align-items: center;
            min-width: 100%;
        }
    }
}
