/*!
 * Wellness Styles
 */

@import "maintenance-window";

#{$microapp-selector} {

    #wellness-tab {
        font-size: 0.91em;
    }

    table.wellness-summary-table {
        border-collapse: collapse;

        td {
            text-align: center;
            padding: 8px;
            width: 78px;
            border-bottom: 1px solid $grid-border;
        }

        th {
            text-align: center;
            border-bottom: 1px solid #111;
            font-size: 12px;
            padding-bottom: 5px;
        }

        tr:last-child td {
            border-bottom: 1px solid #111;
        }

        .wellness-category {
            width: 130px;
            text-align: left;
            font-size: 12px;
            font-weight: bold;
        }

        .highlight-badge {

            &[data-count="0"] {
                display: none;
            }

            &[data-severity="Critical"] {
                background-color: $critical-color;
            }
            &[data-severity="Urgent"] {
                background-color: $urgent-color;
            }
            &[data-severity="Important"] {
                background-color: $important-color;
            }
            &[data-severity="For Review"] {
                background-color: $for-review-color;
            }
        }
    }

    #summary-filter {
        float: right;
        font-size: 0.9em;
        display: inline-block;
        vertical-align: middle;

        .checkbox-label {
            margin-left: 5px;
        }
    }

    .sub-title {
        font-size: 1.2em;
        font-weight: bold;
    }

    .wellness-btn {
        display: none;
    }

    .wellness-text {
        line-height: 1em;
    }

    #email-success {
        color: #0d4566;
        display: none;
    }

    #email-fail {
        color: red;
        display: none;
    }

    #wellness-issues-table {

        th {
            text-align: left;
        }

        td div.severity {
            text-align: center;
            font-size: .95em;
        }

        td .link {
            text-decoration: underline;
            cursor: pointer;
        }

        td .link {
            outline: none;
        }
        td .link:hover {
            text-decoration: none;
        }
        td .link span {
            z-index: 10;
            display: none;
            padding: 2px;
            line-height: 12px;
            margin-top: -20px;
            margin-left: 3px;
        }
        td .link span.multi-row {
            margin-top: -45px;
            margin-left: 12px;
        }
        td .link:hover span {
            display: inline;
            position: absolute;
            color: #111;
            border: 1px solid #dca;
            background: white;
        }

        .array-edit.link {
            float: left;
        }

        .array-management-ui.link {
            padding-left: 7px;

            &:hover i {
                font-weight: bold;
            }
        }

        .ack-timestamp {
            display: block;
            font-size: 0.9em;
        }

    }

    .empty-wellness {
        border: 1px solid #ccc;
        padding: 5px 10px 5px 10px;

        span {
            display: inline-block;
        }
    }

    #wellness-last-update {
        padding-left: 10px;
        padding-bottom: 10px
    }

    .wellness-summary {
        display: none;
        float: left;
        padding-left: 10px
    }

    .empty-wellness {
        display: none;
        float: left;
        padding-left: 10px;
    }

    #app[data-asset-count="0"] #wellness-wrapper1 {
        display: none;
    }
    #wellness-wrapper1 {
        float: right;
    }

    #wellness-info {
        width: 300px;
        margin-top: 20px;
        padding-left: 15px;

        li {
            padding: 5px;
        }
    }

    .wellness-dialog {
        background-color: white;
        font-size: .9em;
    }

    .wellness-details {
        clear: both;
        position: relative;
        top: 20px;
        padding-left: 10px;

        #wellness-issues-container {
            display: none;
        }
    }

    #issues-details-title-container {

        #issues-details-title {
            float: left;
        }

        &:after {
            content: '';
            display: block;
            clear: both;
            padding-bottom: 18px;
        }
    }

    #wellness-issues-grid-container {
        display: none;
    }

    .wellness-issue-action {
        #button-list {
            text-align: center;
        }
        #status {
            margin-right: 5px;
        }
        #action p {
            padding-top: 1em;
        }
    }

    #wellness-summary-toolbar {
        padding-top: 12px;
    }

    #wellness-settings-form {
        ul.email-list {
            list-style: none;
            margin-left: 90px;
        }
    }

}
