#{$microapp-selector} {
    @import "~bootstrap-datepicker/dist/css/bootstrap-datepicker3.standalone.min.css";

    .maintenance-window-view {
        font-size: $next-global-font-size;

        #side-panel-region .list-group {
            overflow-y: scroll;
            max-height: 500px;
        }

        .empty-details {
            text-align: center;
        }
    }

    .maintenance-window-details {
        label {
            text-align: right;
            font-size: 1em;
        }

        .input-group-addon {
            display: flex;
            align-items: center;
            padding: 0 8px;
            border: 1px solid $medium-grey-01;
            border-left: 0;
            background-color: $light-grey-01;
        }

        .button-container {
            text-align: center;
            padding-top: 30px;

            button {
                width: 96px;
                margin: 5px auto;
            }
        }

        select,
        [type="text"] {
            height: initial;
            font-size: 1em;
            padding: 0.35em;
        }
    }

    .maintenance-window-modal .maintenance-window-details {

        [data-field="arrays"],
        .toolbar {
            display: none;
        }
    }

    .maintenance-window-status {
        text-align: center;

        .callout {
            margin-top: -10px;
            margin-bottom: -5px;
            display: inline-block;
        }
    }
}
