/*!
 * GridView styling
 */
@import "~foundation-sites/scss/forms/select";

#{$microapp-selector} {

    // GridView widget
    //   general purpose widget for rendering grids
    .grid-view {

        // Shouldn't need to specify this here since we are importing elmer's foundation-settings,
        // but for some reason (webpack sass loader?) we are picking up foundation defaults
        $table-row-hover: #e1f4fa;

        .toolbar {
            float: left;
        }

        .dataTables_wrapper {
            table {
                border-collapse: collapse;
            }

            thead {
                border-top: 1px solid $light-grey-03;

                tr {
                    border-bottom: 2px solid #999;
                }

                th {
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    overflow: visible;
                    font-weight: normal;
                    text-transform: uppercase;
                    border-right: 1px solid $medium-grey-03;

                    &:last-child {
                        border-right: none;
                    }
                }
            }

            tbody {
                tr {
                    border-bottom: 2px solid #999;

                    &.rowgroup-header td {
                        font-weight: bold;
                        //font-size: 1.2em;
                        background-color: silver;
                        border-top: 1px solid;
                    }

                    &:nth-child(odd),
                    &:nth-child(even) {
                        border-bottom: 1px solid $light-gray;
                        &:hover {

                            background-color: #e1f4fa;
                        }
                    }
                }
            }

            .dataTables_processing {
                border: 1px solid #ccc;
                background-color: #ccc;
            }

            .dataTables_paginate {
                font-size: .85em;
            }

            .dataTables_filter input {
                width: 200px !important;
                margin-bottom: 10px;
            }

            .dataTables_length {
                margin-top: 10px;
            }

            tbody tr.selected,
            tbody tr.selected:hover {
                background-color: $table-row-hover;
            }
        }

        .details-control {
            cursor: pointer;
        }

        th.row-selection-checkbox {
            padding-left: 10px;
        }

        .grid-child-row {
            .grid-child-label {
                font-weight: bold;
            }
        }
    }

    .toggle-filters-link {
        cursor: pointer;
        width: 150px;
        height: 25px;

        .ui-icon {
            margin-right: 5px;
        }

        SPAN {
            display: inline-block;
        }
    }

    .grid-filter-wrapper {
        position: relative;
        border: 1px solid #ccc;
        margin-top: 0px;
        margin-bottom: 20px;
        padding-top: 10px;

        .grid-filter-section {
            padding-left: 10px;
            margin-right: 50px;

            .filter-component {
                display: inline-block;
                padding-bottom: 10px;
                margin-right: 10px;

                label {
                    display: block;
                    margin-right: 5px;
                }
            }
        }

        .clear-filters {
            cursor: pointer;
            font-size: 0.85em;
            margin-right: 1em;
            position: relative;
            top: -30px;
            float: right;

            .ui-icon {
                margin-top: 3px;
                vertical-align: bottom;
            }

            SPAN {
                display: inline-block;
            }
        }

        .ms-drop {
            width: auto;
            overflow: visible;
        }

        .ms-drop label {
            color: $black;
        }

        .ms-choice {
            border-radius: 0;
            border: 1px solid $black;
        }

        .ms-choice > div,
        .ms-choice > div.open {
            @include form-select;
            // @include background-triangle($medium-grey-02);
            height: 25px;
            // padding: 0px;
            border: none;
            background-color: transparent;
        }

        .ms-select-all input[type="checkbox"] {
            display: none;
        }

    }
}
